// offlineManager.js - „Ç™„Éï„É©„Ç§„É≥ÂØæÂøú„Éá„Éº„ÇøÁÆ°ÁêÜ

class OfflineManager {
  constructor() {
    this.isOnline = navigator.onLine;
    this.syncQueue = [];
    this.dbName = 'MuscleRotationDB';
    this.dbVersion = 1;
    this.db = null;
    this.syncInProgress = false;
    this.initializeIndexedDB();
    this.setupEventListeners();
  }

  /**
   * IndexedDB„ÇíÂàùÊúüÂåñ
   */
  async initializeIndexedDB() {
    try {
      this.db = await this.openDatabase();
      console.log('‚úÖ IndexedDB initialized');

      // Êó¢Â≠ò„ÅÆÂêåÊúü„Ç≠„É•„Éº„ÇíÂæ©ÂÖÉ
      await this.restoreSyncQueue();
    } catch (error) {
      console.error('‚ùå IndexedDB initialization failed:', error);
      // „Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØ„Å®„Åó„Å¶LocalStorage„Çí‰ΩøÁî®
      this.useLocalStorageFallback = true;
    }
  }

  /**
   * „Éá„Éº„Çø„Éô„Éº„Çπ„ÇíÈñã„Åè
   * @returns {Promise<IDBDatabase>}
   */
  openDatabase() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, this.dbVersion);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve(request.result);

      request.onupgradeneeded = (event) => {
        const db = event.target.result;

        // „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥„Çπ„Éà„Ç¢
        if (!db.objectStoreNames.contains('workoutSessions')) {
          const workoutStore = db.createObjectStore('workoutSessions', {
            keyPath: 'id',
            autoIncrement: true,
          });
          workoutStore.createIndex('date', 'workout_date', { unique: false });
          workoutStore.createIndex('userId', 'user_id', { unique: false });
          workoutStore.createIndex('syncStatus', 'sync_status', {
            unique: false,
          });
        }

        // „Éà„É¨„Éº„Éã„É≥„Ç∞„É≠„Ç∞„Çπ„Éà„Ç¢
        if (!db.objectStoreNames.contains('trainingLogs')) {
          const logsStore = db.createObjectStore('trainingLogs', {
            keyPath: 'id',
            autoIncrement: true,
          });
          logsStore.createIndex('sessionId', 'workout_session_id', {
            unique: false,
          });
          logsStore.createIndex('date', 'workout_date', { unique: false });
          logsStore.createIndex('syncStatus', 'sync_status', { unique: false });
        }

        // ÂêåÊúü„Ç≠„É•„Éº„Çπ„Éà„Ç¢
        if (!db.objectStoreNames.contains('syncQueue')) {
          const syncStore = db.createObjectStore('syncQueue', {
            keyPath: 'id',
            autoIncrement: true,
          });
          syncStore.createIndex('priority', 'priority', { unique: false });
          syncStore.createIndex('timestamp', 'timestamp', { unique: false });
        }

        // Ë®≠ÂÆö„Çπ„Éà„Ç¢
        if (!db.objectStoreNames.contains('settings')) {
          db.createObjectStore('settings', { keyPath: 'key' });
        }

        console.log('üìä IndexedDB schema created');
      };
    });
  }

  /**
   * „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíË®≠ÂÆö
   */
  setupEventListeners() {
    // „Ç™„É≥„É©„Ç§„É≥/„Ç™„Éï„É©„Ç§„É≥Áä∂ÊÖã„ÅÆÁõ£Ë¶ñ
    window.addEventListener('online', () => {
      console.log('üåê „Ç™„É≥„É©„Ç§„É≥„Å´„Å™„Çä„Åæ„Åó„Åü');
      this.isOnline = true;
      this.handleOnlineStatusChange(true);
    });

    window.addEventListener('offline', () => {
      console.log('üì¥ „Ç™„Éï„É©„Ç§„É≥„Å´„Å™„Çä„Åæ„Åó„Åü');
      this.isOnline = false;
      this.handleOnlineStatusChange(false);
    });

    // „Éö„Éº„Ç∏„Ç¢„É≥„É≠„Éº„ÉâÊôÇ„ÅÆÂá¶ÁêÜ
    window.addEventListener('beforeunload', () => {
      this.persistSyncQueue();
    });

    // Visibility API „Åß„Çø„Éñ„ÅÆÁä∂ÊÖã„ÇíÁõ£Ë¶ñ
    document.addEventListener('visibilitychange', () => {
      if (!document.hidden && this.isOnline) {
        this.processSyncQueue();
      }
    });
  }

  /**
   * „Ç™„É≥„É©„Ç§„É≥Áä∂ÊÖãÂ§âÊõ¥„ÅÆÂá¶ÁêÜ
   * @param {boolean} isOnline - „Ç™„É≥„É©„Ç§„É≥Áä∂ÊÖã
   */
  async handleOnlineStatusChange(isOnline) {
    if (isOnline) {
      // „Ç™„É≥„É©„Ç§„É≥Âæ©Â∏∞ÊôÇ„ÅÆÂá¶ÁêÜ
      document.body.classList.remove('offline-mode');

      // ÂêåÊúü„Ç≠„É•„Éº„ÇíÂá¶ÁêÜ
      await this.processSyncQueue();

      // UIÊõ¥Êñ∞„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
      window.dispatchEvent(new CustomEvent('connectionRestored'));
    } else {
      // „Ç™„Éï„É©„Ç§„É≥ÊôÇ„ÅÆÂá¶ÁêÜ
      document.body.classList.add('offline-mode');

      // UIÊõ¥Êñ∞„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
      window.dispatchEvent(new CustomEvent('connectionLost'));
    }
  }

  /**
   * „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥„Çí‰øùÂ≠òÔºà„Ç™„Éï„É©„Ç§„É≥ÂØæÂøúÔºâ
   * @param {Object} sessionData - „Çª„ÉÉ„Ç∑„Éß„É≥„Éá„Éº„Çø
   * @returns {Promise<Object>} ‰øùÂ≠òÁµêÊûú
   */
  async saveWorkoutSession(sessionData) {
    const session = {
      ...sessionData,
      id:
        sessionData.id ||
        `offline_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      sync_status: this.isOnline ? 'synced' : 'pending',
      created_at: new Date().toISOString(),
      updated_at: new Date().toISOString(),
    };

    try {
      if (this.db) {
        // IndexedDB„Å´‰øùÂ≠ò
        await this.saveToIndexedDB('workoutSessions', session);
      } else {
        // LocalStorage„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØ
        await this.saveToLocalStorage('workoutSessions', session);
      }

      // „Ç™„Éï„É©„Ç§„É≥ÊôÇ„ÅØÂêåÊúü„Ç≠„É•„Éº„Å´ËøΩÂä†
      if (!this.isOnline) {
        await this.addToSyncQueue({
          type: 'CREATE_WORKOUT_SESSION',
          data: session,
          priority: 1,
        });
      }

      console.log(
        `üíæ „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥‰øùÂ≠ò: ${session.id} (${session.sync_status})`
      );
      return session;
    } catch (error) {
      console.error('‚ùå „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥‰øùÂ≠òÂ§±Êïó:', error);
      throw error;
    }
  }

  /**
   * „Éà„É¨„Éº„Éã„É≥„Ç∞„É≠„Ç∞„Çí‰øùÂ≠òÔºà„Ç™„Éï„É©„Ç§„É≥ÂØæÂøúÔºâ
   * @param {Array} logs - „É≠„Ç∞ÈÖçÂàó
   * @returns {Promise<Array>} ‰øùÂ≠òÁµêÊûú
   */
  async saveTrainingLogs(logs) {
    const processedLogs = logs.map((log) => ({
      ...log,
      id:
        log.id ||
        `offline_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      sync_status: this.isOnline ? 'synced' : 'pending',
      created_at: new Date().toISOString(),
    }));

    try {
      if (this.db) {
        // IndexedDB„Å´‰∏ÄÊã¨‰øùÂ≠ò
        await Promise.all(
          processedLogs.map((log) => this.saveToIndexedDB('trainingLogs', log))
        );
      } else {
        // LocalStorage„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØ
        await Promise.all(
          processedLogs.map((log) =>
            this.saveToLocalStorage('trainingLogs', log)
          )
        );
      }

      // „Ç™„Éï„É©„Ç§„É≥ÊôÇ„ÅØÂêåÊúü„Ç≠„É•„Éº„Å´ËøΩÂä†
      if (!this.isOnline) {
        await this.addToSyncQueue({
          type: 'CREATE_TRAINING_LOGS',
          data: processedLogs,
          priority: 2,
        });
      }

      console.log(
        `üíæ „Éà„É¨„Éº„Éã„É≥„Ç∞„É≠„Ç∞‰øùÂ≠ò: ${processedLogs.length}‰ª∂ (${processedLogs[0]?.sync_status})`
      );
      return processedLogs;
    } catch (error) {
      console.error('‚ùå „Éà„É¨„Éº„Éã„É≥„Ç∞„É≠„Ç∞‰øùÂ≠òÂ§±Êïó:', error);
      throw error;
    }
  }

  /**
   * „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥„ÇíÂèñÂæó
   * @param {Object} options - ÂèñÂæó„Ç™„Éó„Ç∑„Éß„É≥
   * @returns {Promise<Array>} „Çª„ÉÉ„Ç∑„Éß„É≥ÈÖçÂàó
   */
  async getWorkoutSessions(options = {}) {
    const { limit = 20, offset = 0, userId = null } = options;

    try {
      if (this.db) {
        return await this.getFromIndexedDB('workoutSessions', {
          limit,
          offset,
          userId,
        });
      } else {
        return await this.getFromLocalStorage('workoutSessions', {
          limit,
          offset,
          userId,
        });
      }
    } catch (error) {
      console.error('‚ùå „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥ÂèñÂæóÂ§±Êïó:', error);
      return [];
    }
  }

  /**
   * IndexedDB„Å´„Éá„Éº„Çø„Çí‰øùÂ≠ò
   * @param {string} storeName - „Çπ„Éà„Ç¢Âêç
   * @param {Object} data - „Éá„Éº„Çø
   * @returns {Promise<void>}
   */
  saveToIndexedDB(storeName, data) {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction([storeName], 'readwrite');
      const store = transaction.objectStore(storeName);
      const request = store.put(data);

      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  /**
   * IndexedDB„Åã„Çâ„Éá„Éº„Çø„ÇíÂèñÂæó
   * @param {string} storeName - „Çπ„Éà„Ç¢Âêç
   * @param {Object} options - „Ç™„Éó„Ç∑„Éß„É≥
   * @returns {Promise<Array>}
   */
  getFromIndexedDB(storeName, options = {}) {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction([storeName], 'readonly');
      const store = transaction.objectStore(storeName);
      const request = store.getAll();

      request.onsuccess = () => {
        let results = request.result || [];

        // „Éï„Ç£„É´„Çø„É™„É≥„Ç∞
        if (options.userId) {
          results = results.filter((item) => item.user_id === options.userId);
        }

        // „ÇΩ„Éº„ÉàÔºàÊó•‰ªòÈôçÈ†ÜÔºâ
        results.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));

        // „Éö„Éº„Ç∏„Éç„Éº„Ç∑„Éß„É≥
        const { limit = 20, offset = 0 } = options;
        results = results.slice(offset, offset + limit);

        resolve(results);
      };
      request.onerror = () => reject(request.error);
    });
  }

  /**
   * LocalStorage„Å´„Éá„Éº„Çø„Çí‰øùÂ≠òÔºà„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÔºâ
   * @param {string} storeName - „Çπ„Éà„Ç¢Âêç
   * @param {Object} data - „Éá„Éº„Çø
   * @returns {Promise<void>}
   */
  async saveToLocalStorage(storeName, data) {
    try {
      const key = `offline_${storeName}`;
      const existing = JSON.parse(localStorage.getItem(key) || '[]');

      // Êó¢Â≠ò„Éá„Éº„Çø„ÅÆÊõ¥Êñ∞„Åæ„Åü„ÅØÊñ∞Ë¶èËøΩÂä†
      const index = existing.findIndex((item) => item.id === data.id);
      if (index >= 0) {
        existing[index] = data;
      } else {
        existing.push(data);
      }

      // ÊúÄÊñ∞1000‰ª∂„ÅÆ„Åø‰øùÊåÅ
      if (existing.length > 1000) {
        existing.splice(0, existing.length - 1000);
      }

      localStorage.setItem(key, JSON.stringify(existing));
    } catch (error) {
      console.error('LocalStorage‰øùÂ≠ò„Ç®„É©„Éº:', error);
      throw error;
    }
  }

  /**
   * LocalStorage„Åã„Çâ„Éá„Éº„Çø„ÇíÂèñÂæóÔºà„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÔºâ
   * @param {string} storeName - „Çπ„Éà„Ç¢Âêç
   * @param {Object} options - „Ç™„Éó„Ç∑„Éß„É≥
   * @returns {Promise<Array>}
   */
  async getFromLocalStorage(storeName, options = {}) {
    try {
      const key = `offline_${storeName}`;
      let results = JSON.parse(localStorage.getItem(key) || '[]');

      // „Éï„Ç£„É´„Çø„É™„É≥„Ç∞
      if (options.userId) {
        results = results.filter((item) => item.user_id === options.userId);
      }

      // „ÇΩ„Éº„Éà
      results.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));

      // „Éö„Éº„Ç∏„Éç„Éº„Ç∑„Éß„É≥
      const { limit = 20, offset = 0 } = options;
      return results.slice(offset, offset + limit);
    } catch (error) {
      console.error('LocalStorageÂèñÂæó„Ç®„É©„Éº:', error);
      return [];
    }
  }

  /**
   * ÂêåÊúü„Ç≠„É•„Éº„Å´„Ç¢„Ç§„ÉÜ„É†„ÇíËøΩÂä†
   * @param {Object} item - ÂêåÊúü„Ç¢„Ç§„ÉÜ„É†
   * @returns {Promise<void>}
   */
  async addToSyncQueue(item) {
    const queueItem = {
      ...item,
      id: `sync_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      timestamp: Date.now(),
      retryCount: 0,
      maxRetries: 3,
    };

    this.syncQueue.push(queueItem);

    // IndexedDB„Å´„ÇÇ‰øùÂ≠ò
    if (this.db) {
      try {
        await this.saveToIndexedDB('syncQueue', queueItem);
      } catch (error) {
        console.warn('ÂêåÊúü„Ç≠„É•„Éº„ÅÆÊ∞∏Á∂öÂåñ„Å´Â§±Êïó:', error);
      }
    }

    console.log(`üì§ ÂêåÊúü„Ç≠„É•„Éº„Å´ËøΩÂä†: ${item.type}`);
  }

  /**
   * ÂêåÊúü„Ç≠„É•„Éº„ÇíÂá¶ÁêÜ
   * @returns {Promise<void>}
   */
  async processSyncQueue() {
    if (this.syncInProgress || !this.isOnline || this.syncQueue.length === 0) {
      return;
    }

    this.syncInProgress = true;
    console.log(`üì§ ÂêåÊúüÈñãÂßã: ${this.syncQueue.length}‰ª∂`);

    // ÂÑ™ÂÖàÂ∫¶È†Ü„Å´„ÇΩ„Éº„Éà
    this.syncQueue.sort((a, b) => (a.priority || 0) - (b.priority || 0));

    const results = {
      success: 0,
      failed: 0,
      total: this.syncQueue.length,
    };

    // ÂêåÊúüÂá¶ÁêÜ„Çí‰∏¶ÂàóÂÆüË°åÔºàÊúÄÂ§ß3‰ª∂Ôºâ
    const concurrency = 3;
    for (let i = 0; i < this.syncQueue.length; i += concurrency) {
      const batch = this.syncQueue.slice(i, i + concurrency);

      const batchPromises = batch.map(async (item, index) => {
        try {
          await this.syncItem(item);
          results.success++;

          // ÊàêÂäü„Åó„Åü„Ç¢„Ç§„ÉÜ„É†„Çí„Ç≠„É•„Éº„Åã„ÇâÂâäÈô§
          const queueIndex = this.syncQueue.findIndex((q) => q.id === item.id);
          if (queueIndex >= 0) {
            this.syncQueue.splice(queueIndex, 1);
          }

          // IndexedDB„Åã„Çâ„ÇÇÂâäÈô§
          if (this.db) {
            await this.deleteFromIndexedDB('syncQueue', item.id);
          }
        } catch (error) {
          results.failed++;
          item.retryCount = (item.retryCount || 0) + 1;
          item.lastError = error.message;

          console.error(`‚ùå ÂêåÊúüÂ§±Êïó: ${item.type}`, error);

          // ÊúÄÂ§ß„É™„Éà„É©„Ç§ÂõûÊï∞„Å´ÈÅî„Åó„ÅüÂ†¥Âêà„ÅØÂâäÈô§
          if (item.retryCount >= item.maxRetries) {
            const queueIndex = this.syncQueue.findIndex(
              (q) => q.id === item.id
            );
            if (queueIndex >= 0) {
              this.syncQueue.splice(queueIndex, 1);
            }
            console.warn(
              `‚ö†Ô∏è ÂêåÊúüË´¶„ÇÅ: ${item.type} (${item.retryCount}ÂõûË©¶Ë°å)`
            );
          }
        }
      });

      await Promise.allSettled(batchPromises);
    }

    this.syncInProgress = false;

    // ÂêåÊúüÁµêÊûú„ÇíÈÄöÁü•
    if (results.success > 0) {
      window.dispatchEvent(
        new CustomEvent('syncCompleted', {
          detail: { success: results.success, failed: results.failed },
        })
      );
    }

    console.log(
      `‚úÖ ÂêåÊúüÂÆå‰∫Ü: ÊàêÂäü ${results.success}‰ª∂, Â§±Êïó ${results.failed}‰ª∂`
    );
  }

  /**
   * ÂÄãÂà•„Ç¢„Ç§„ÉÜ„É†„ÇíÂêåÊúü
   * @param {Object} item - ÂêåÊúü„Ç¢„Ç§„ÉÜ„É†
   * @returns {Promise<void>}
   */
  async syncItem(item) {
    switch (item.type) {
      case 'CREATE_WORKOUT_SESSION':
        await this.syncWorkoutSession(item.data);
        break;
      case 'CREATE_TRAINING_LOGS':
        await this.syncTrainingLogs(item.data);
        break;
      case 'UPDATE_SETTINGS':
        await this.syncSettings(item.data);
        break;
      default:
        throw new Error(`Êú™ÂØæÂøú„ÅÆÂêåÊúü„Çø„Ç§„Éó: ${item.type}`);
    }
  }

  /**
   * „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Çª„ÉÉ„Ç∑„Éß„É≥„ÇíÂêåÊúü
   * @param {Object} sessionData - „Çª„ÉÉ„Ç∑„Éß„É≥„Éá„Éº„Çø
   * @returns {Promise<void>}
   */
  async syncWorkoutSession(sessionData) {
    // ÂÆüÈöõ„ÅÆSupabase„Çµ„Éº„Éì„Çπ„Çí‰ΩøÁî®„Åó„Å¶ÂêåÊúü
    if (window.supabaseService && window.supabaseService.saveWorkout) {
      const result = await window.supabaseService.saveWorkout(sessionData);

      // „É≠„Éº„Ç´„É´„Éá„Éº„Çø„ÅÆÂêåÊúüÁä∂ÊÖã„ÇíÊõ¥Êñ∞
      sessionData.sync_status = 'synced';
      sessionData.server_id = result.id;

      if (this.db) {
        await this.saveToIndexedDB('workoutSessions', sessionData);
      }
    }
  }

  /**
   * „Éà„É¨„Éº„Éã„É≥„Ç∞„É≠„Ç∞„ÇíÂêåÊúü
   * @param {Array} logs - „É≠„Ç∞ÈÖçÂàó
   * @returns {Promise<void>}
   */
  async syncTrainingLogs(logs) {
    if (window.supabaseService && window.supabaseService.saveTrainingLogs) {
      const result = await window.supabaseService.saveTrainingLogs(logs);

      // „É≠„Éº„Ç´„É´„Éá„Éº„Çø„ÅÆÂêåÊúüÁä∂ÊÖã„ÇíÊõ¥Êñ∞
      logs.forEach((log, index) => {
        log.sync_status = 'synced';
        log.server_id = result[index]?.id;
      });

      if (this.db) {
        await Promise.all(
          logs.map((log) => this.saveToIndexedDB('trainingLogs', log))
        );
      }
    }
  }

  /**
   * Ë®≠ÂÆö„ÇíÂêåÊúü
   * @param {Object} settings - Ë®≠ÂÆö„Éá„Éº„Çø
   * @returns {Promise<void>}
   */
  async syncSettings(settings) {
    if (window.supabaseService && window.supabaseService.saveUserProfile) {
      await window.supabaseService.saveUserProfile(settings);
    }
  }

  /**
   * ÂêåÊúü„Ç≠„É•„Éº„ÇíÂæ©ÂÖÉ
   * @returns {Promise<void>}
   */
  async restoreSyncQueue() {
    if (!this.db) {
      return;
    }

    try {
      const transaction = this.db.transaction(['syncQueue'], 'readonly');
      const store = transaction.objectStore('syncQueue');
      const request = store.getAll();

      request.onsuccess = () => {
        this.syncQueue = request.result || [];
        console.log(`üì• ÂêåÊúü„Ç≠„É•„ÉºÂæ©ÂÖÉ: ${this.syncQueue.length}‰ª∂`);
      };
    } catch (error) {
      console.warn('ÂêåÊúü„Ç≠„É•„ÉºÂæ©ÂÖÉ„Å´Â§±Êïó:', error);
    }
  }

  /**
   * ÂêåÊúü„Ç≠„É•„Éº„ÇíÊ∞∏Á∂öÂåñ
   */
  persistSyncQueue() {
    try {
      localStorage.setItem('syncQueue', JSON.stringify(this.syncQueue));
    } catch (error) {
      console.warn('ÂêåÊúü„Ç≠„É•„ÉºÊ∞∏Á∂öÂåñ„Å´Â§±Êïó:', error);
    }
  }

  /**
   * IndexedDB„Åã„Çâ„Éá„Éº„Çø„ÇíÂâäÈô§
   * @param {string} storeName - „Çπ„Éà„Ç¢Âêç
   * @param {string} id - ID
   * @returns {Promise<void>}
   */
  deleteFromIndexedDB(storeName, id) {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction([storeName], 'readwrite');
      const store = transaction.objectStore(storeName);
      const request = store.delete(id);

      request.onsuccess = () => resolve();
      request.onerror = () => reject(request.error);
    });
  }

  /**
   * „Ç™„Éï„É©„Ç§„É≥Áä∂ÊÖã„ÇíÂèñÂæó
   * @returns {boolean} „Ç™„Éï„É©„Ç§„É≥Áä∂ÊÖã
   */
  isOffline() {
    return !this.isOnline;
  }

  /**
   * ÂêåÊúüÂæÖ„Å°„Ç¢„Ç§„ÉÜ„É†Êï∞„ÇíÂèñÂæó
   * @returns {number} ÂêåÊúüÂæÖ„Å°„Ç¢„Ç§„ÉÜ„É†Êï∞
   */
  getPendingSyncCount() {
    return this.syncQueue.length;
  }

  /**
   * Áµ±Ë®àÊÉÖÂ†±„ÇíÂèñÂæó
   * @returns {Object} Áµ±Ë®àÊÉÖÂ†±
   */
  getStats() {
    return {
      isOnline: this.isOnline,
      pendingSyncItems: this.syncQueue.length,
      syncInProgress: this.syncInProgress,
      useLocalStorageFallback: this.useLocalStorageFallback,
      dbInitialized: !!this.db,
    };
  }

  /**
   * „ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó
   */
  cleanup() {
    this.persistSyncQueue();
    if (this.db) {
      this.db.close();
    }
  }
}

